.card_size_l {
  grid-column: span 12;
  grid-column-gap: 16px;
  grid-template-rows: auto auto 1rem;
}

.card_size_l .card-pic {
  grid-column: span 8;
  grid-row: span 3;
}

.card_size_l .card-link {
  grid-column: span 4
}

.card_size_l .card-desc {
  grid-column: span 4;
}

.card_size_l .card-controls {
  grid-column: span 4;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
}



